*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background-color: #231955;
    color:#FFE5B4;
}

h2{
    color:#E8AA42;
}

.title-header h2{
    font-size: 2.5rem;
    padding: 1rem;
}

.container{
    width: 50%;
    text-align: center;
    margin: 10% auto;
    padding-top: 2rem;
}

.bg-1{
    background-color: #1F4690;
}

form div{
    padding:1rem 0;
}

.form-container{
    width: 50%;
    margin: 10% auto;
    text-align: center;
    padding:2rem 0;
}

.form-container label{
    display: inline-block;
    width: 150px;

}

.Button-spacer{
    margin: 10px 0;
}
.search-box{
   max-width: 55rem;
   margin-bottom: 2rem;
   width: 100%;
   padding: .5rem;
   text-align: center;
}

.Search-res{
    padding: .3rem 0;
    font-size: 1.3rem;
}
.contact-details{
    font-size: 1.3rem;
    display: inline-block;
    padding: 0.5rem 0.5rem 0.5rem 0;
}

.Delete-bt,.Add-bt{
    color: white;
    background-color: black;
    border: none;
    padding:.3rem;
    cursor: pointer;
}

.Add-bt{
    font-size:1rem;
}

@media screen and (max-width:43rem) {
    .container, .form-container{
        width:80%;
    }

    
    .title-header h2{
        font-size: 1.5rem;
 
    }
   
}   

@media screen and (max-width:62rem) {
    .container, .form-container{
        width:80%;
    }

     .title-header h2{
        font-size: 1.5rem;

    }
}   